@import './reset';

$text-color: #383838;
$blue: #68c3c0;

@import './font';
@import './hud/main';

* {
	box-sizing: border-box;
}

html,
body {
	width: 100%;
	height: 100%;

	background: linear-gradient(#FAFAFA, #D9D9D9);

	overflow: hidden;
}

.dg {
	z-index: 2 !important;
}

#app-wrapper {
	position: relative;
	width: 100%;
	height: 100%;
	min-height: 100%;
}

#main {
	position: relative;
	width: 100%;
	height: 100%;

	opacity: 1;
}

#main canvas {
	display: block;
}

.bold {
	font-weight: bold;
}